<template>
	<section>
	     <!---->
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getTemplates"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="templates" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="50">
			</el-table-column>
			<el-table-column prop="templateName" label="" width="120" >
			</el-table-column>
			<el-table-column prop="alreadyConfig" label="" width="120" :formatter="format">
			</el-table-column>
			<el-table-column prop="description" label="" width="120" >
			</el-table-column>
			<el-table-column prop="createTime" label="" width="120" >
			</el-table-column>
			<el-table-column prop="defaultTemplate" label="" width="120" :formatter="formatDefault">
			</el-table-column>
			<el-table-column prop="lastUpdateTime" label="" min-width="120" >
			</el-table-column>
			<el-table-column label="" width="300">
				<template scope="scope">
				    <el-button size="small" @click="setDefault(scope.$index,scope.row)"></el-button>
					<el-button size="small" @click="handleEdit(scope.$index,scope.row)"></el-button>
					<el-button type="danger" size="small" @click="remove(scope.$index,scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="total,sizes,prev, pager, next" @current-change="handleCurrentChange"
			@size-change="handleSizeChange"  :total="total" :page-sizes ="pageSizes" :page-size="pageSize" style="float:right;">
			</el-pagination>
		</el-col>

	

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addTemplate" label-width="80px" >
				<el-form-item label="" prop="templateName">
					<el-input v-model="addTemplate.templateName" ></el-input>
				</el-form-item>
				<el-form-item label="" prop="description">
				     <el-input v-model="addTemplate.description"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addTemplate.defaultTemplate">
						<el-radio class="radio" :label="true"></el-radio>
						<el-radio class="radio" :label="false"></el-radio>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>

	</section>
</template>

<script>
	export default{
		data() {
			return{
				/*	pageSize 
				pageSizes  
				page  
				total  
				pages  */
				filters: {
					name: [],
				},
				addFormVisible: false,
				page: 1,
				pageSize: 10,
				pageSizes: [10,20,30],
				listLoading: false,
				templates: [],
				pages: 0,
				total: 0,
				addTemplate: {
					templateName: '',
					description: '',
					defaultTemplate: false,
				},
				sels: [],
				addLoading: false,
				
			}
		},
		methods: {
			format: function(row,column){
				return row.alreadyConfig == false ? '' : '';
			},
			formatDefault: function(row,column){
				return row.defaultTemplate == false ? '' : '';
			},
			getTemplates() {
				let param = {
					page: this.page,
					rows: this.pageSize,
					templateName: this.filters.name,
				};
				this.listLoading = true;
				let url = '/api/reviewFlowTemplate/queryReviewFlowTemplateByName';
				this.func.ajaxPost(url,param,res =>{
					this.templates = res.data.records;
					this.pages = res.data.pages;
					this.total = res.data.total;
					this.listLoading = false;
				});
			},
			handleAdd() {
				this.addFormVisible = true;
				this.addTemplate = {
					templateName: '',
					description: '',
					defaultTemplate: false,
				}
			},
			selsChange(sels) {
				this.sels = sels;
			},
			handleEdit: function(index,row){
				//let templateId = row.templateId;
				//sessionStorage.setItem('templateId',this.sels[0].templateId);
				sessionStorage.setItem('templateId',row.templateId);
				this.$router.push({path:'/setCheckors'});
			},
			
			//
			batchRemove: function() {
				let ids = this.sels.map(item =>item.templateId).join(",").toString();
				this.$confirm('','',{
					type:'warning'
				}).then(()=>{
					this.listLoading = true;
					let params = {templateIds: ids};
					let url = '/api/reviewFlowTemplate/deleteByTemplateId';
					this.func.ajaxPost(url,params,res =>{
						if(res.data.flag == true){
							this.$message({
							message: '',
							type: 'success'
						    });
						    this.listLoading = false;
						    this.getTemplates();
						}
					})
				})
			},
			handleCurrentChange(val){
				this.page = val;
				this.getTemplates();
			},
			handleSizeChange(val) {
				this.pageSize = val;
				this.getTemplates();
			},
			addSubmit: function(){
				this.addLoading = true;
				let param = Object.assign({},this.addTemplate);
				param.assure = "no";
				let url = '/api/reviewFlowTemplate/addReviewFlowTemplate';
				this.func.ajaxPost(url,param,res =>{
					if(res.data.code ==200){
						if(!res.data.changeDefault){
							this.addLoading = false;
							this.addFormVisible = false;
							this.$message({
								message: '',
								type: 'success',
							})
							this.getTemplates();
						}else{
							this.$confirm('','',{}).then(()=>{
								param.assure = "yes";
								this.func.ajaxPost(url,param,res =>{
									if(res.data.code === 200 && !res.data.changeDefault){
										this.addLoading = false;
										this.addFormVisible = false;
										this.$message({
											message: '',
											type: 'success',
										})
										this.getTemplates();
									}
								})
							})
						}

					}
				})
			},
			remove: function(index,row){
				this.$confirm('','',{}).then(()=>{
					this.listLoading = true;
					let params = {templateIds: row.templateId};
					let url = '/api/reviewFlowTemplate/deleteByTemplateId';
					this.func.ajaxPost(url,params,res =>{
						if(res.data.flag == true){
							this.$message({
							message: '',
							type: 'success'
						    });
						    this.listLoading = false;
						    this.getTemplates();
						}
					})
				});
			},
			setDefault: function(index,row){
				let param = {templateId: row.templateId};
				let url = '/api/reviewFlowTemplate/setDefault';
				param.assure = "no";
				this.func.ajaxPost(url,param,res =>{
					if(res.data.code ===200){
						if(!res.data.reminder){
							this.$message({
							message: '',
							type: 'success'
						    });
						    this.listLoading = false;
						    this.getTemplates();
						}else{
							this.$confirm('','',{}).then(()=>{
								param.assure = "yes";
								this.func.ajaxPost(url,param,res =>{
									if(res.data.code ===200){
										this.$message({
											message: '',
											type: 'success'
										});
										this.listLoading = false;
										this.getTemplates();
									}
								})
							})
						}
					}
				})
			},
		},
		mounted() {
			this.getTemplates();
		}
	}
</script>

<style scoped>

</style>


