import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Gogs from './views/Gogs.vue'
import Main from './views/Main.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import Page4 from './views/nav2/Page4.vue'
import Page5 from './views/nav2/Page5.vue'
import Page6 from './views/nav3/Page6.vue'
import FileContent from './views/nav3/index.vue'
import echarts from './views/charts/echarts.vue'
import Repository from './views/repository/index2.vue'
import Model from './views/model/index.vue'
import modelVariable from './views/model/modelVariable.vue' ;
import packageDiagram from './views/model/packageDiagram.vue'
import packageDiagramModel from './views/model/packageDiagramModel.vue'
import packageDiagramVariable from './views/model/packageDiagramVariable.vue'
import modelComponent from  './views/model/ModelComponent.vue'
import modelCont from './views/model/modelCont.vue'
import packageTransfModel from  './views/model/packageTransfModel.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-menu',
        leaf: true,//
        children: [
            { path: '/repository/index2', component: Repository, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-document',
        leaf: true,//
        children: [
            { path: '/model/index', component: Model, name: '' },
            { path: '/model/packageTransfModel', component: packageTransfModel, name: ''},
            { path: '/model/modelCont', component: modelCont, name: ''},
            // { path: '/model/modelVariable', component: modelVariable, name: '' },
            { path: '/model/packageDiagram', component: packageDiagram, name: ''},
            { path: '/model/packageDiagramModel', component: packageDiagramModel, name: '' },
            { path: '/model/packageDiagramVariable', component: packageDiagramVariable, name: '',
                iconCls: 'el-icon-document',
                leaf: true,//
                children: [
                    { path: '/model/ModelComponent', component: modelComponent, name: '' },
                    ]
            },
            // { path: '/model/ModelComponent', component: modelComponent, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-address-card',
        leaf: true,//
        children: [
            // { path: '/page6', component: Page6, name: '' }
            { path: '/nav3/index', component: FileContent, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-share',
        leaf: true,//
        children: [
            { path: '/page4', component: Page4, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-star-on',//class
        children: [
            // { path: '/main', component: Main, name: '', hidden: true },
            { path: '/table', component: Table, name: '' },
            { path: '/form', component: Form, name: '' },
            { path: '/user', component: user, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-delete',
        leaf: true,//
        children: [
            { path: '/page6', component: Page6, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-setting',//class
        children: [
            // { path: '/main', component: Main, name: '', hidden: true },
            { path: '/table', component: Table, name: '' },
            { path: '/form', component: Form, name: '' },
            { path: '/user', component: user, name: '' },
            { path: '/user', component: user, name: '' },
            { path: '/user', component: user, name: '' },
            { path: '/user', component: user, name: '' },
            { path: '/user', component: user, name: '' },
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    },
    //gogs
    {
        path: '/',
        component: Gogs,
        name: '',
        iconCls: 'el-icon-menu',
        leaf: true,//
        children: [
            { path: '/repository/index', component: Repository, name: '' }
        ]
    },
    {
        path: '/',
        component: Gogs,
        name: '',
        iconCls: 'el-icon-document',
        leaf: true,//
        children: [
            { path: '/model/index', component: Model, name: '' }
        ]
    },
    {
        path: '/',
        component: Gogs,
        name: '',
        iconCls: 'fa fa-address-card',
        leaf: true,//
        children: [
            { path: '/page6', component: Page6, name: '' }
        ]
    },
    {
        path: '/',
        component: Gogs,
        name: '',
        iconCls: 'el-icon-share',
        leaf: true,//
        children: [
            { path: '/page6', component: Page6, name: '' }
        ]
    },
    {
        path: '/',
        component: Gogs,
        name: '',
        iconCls: 'el-icon-star-on',//class
        children: [
            { path: '/main', component: Main, name: '' },
            { path: '/table', component: Table, name: '' },
            { path: '/form', component: Form, name: '' },
            { path: '/user', component: user, name: '' },
        ]
    },
    {
        path: '/',
        component: Gogs,
        name: '',
        iconCls: 'el-icon-delete',
        leaf: true,//
        children: [
            { path: '/page6', component: Page6, name: '' }
        ]
    },
    {
        path: '/',
        component: Gogs,
        name: '',
        iconCls: 'el-icon-setting',//class
        children: [
            // { path: '/main', component: Main, name: '', hidden: true },
            { path: '/table', component: Table, name: '' },
            { path: '/form', component: Form, name: '' },
            { path: '/user', component: user, name: '' },
            { path: '/user', component: user, name: '' },
            { path: '/user', component: user, name: '' },
            { path: '/user', component: user, name: '' },
            { path: '/user', component: user, name: '' },
        ]
    },
];

export default routes;


